.header--header_container {
  position: fixed;
  top: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  width: 100%;
  height: 7rem;
  color: white;
  padding: 1em;
  background-color: rgb(58, 57, 57);
  border-bottom: 2px black solid;
}

.title_name {
  height: 100%;
  cursor: default;
}

.title_name h1 {
  display: flex;
  flex-direction: column;
  align-items: start;
  animation: flipTitle 2s;
}

@keyframes flipTitle {
  from {
    transform: rotateX(90deg);
  }
  to {
    transform: rotateX(0deg);
  }
}

.title_name h1 span {
  font-size: 18px;
}

.list {
  list-style-type: none;
  width: 60%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.item span {
  cursor: pointer;
}

.input_search input {
  border: none;
  border-radius: 5px;
  padding: 0.2em;
  background: none;
  border-bottom: 2px solid rgba(0, 0, 0, 0.164);
  color: #fff;
}

.input_search input::placeholder {
  color: #fff;
  opacity: 0.5;
}

@media screen and (max-width: 975px) {
  .title_name h1 {
    display: flex;
    align-items: center;
    font-size: 30px;
    flex-direction: column;
    height: 10em;
  }
  .title_name h1 span {
    text-align: end;
    padding: 0.9em;
    height: 100%;
    font-size: 10px;
  }
}
@media screen and (max-width: 720px) {
  .header--header_container {
    flex-direction: column;
    width: 100%;
  }
  .title_name {
    width: 100%;
  }
  .title_name h1 {
    height: max-content;
    display: flex;
    flex-direction: row;
    justify-content: center;
  }

  .title_name h1 span {
    display: none;
  }

  .list {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
  }

  .item span {
    font-size: 14px;
  }
}
