.modal_container {
  background-color: #222;
  width: 70vw;
  height: 90vh;
  position: fixed;
  top: 50%;
  left: 50%;
  color: #fff;
  transform: translate(-50%, -50%);
  border: none;
  z-index: 999;
  overflow: auto;
}

.header_container {
  padding: 0.8em;
  position: relative;
  height: 6em;
}

.header_container h1 {
  font-size: 2em;
  text-align: center;
  cursor: default;
}

.close_button_container {
  display: flex;
  justify-content: end;
}

.close_button {
  cursor: pointer;
}

.description_container {
  display: flex;
  flex-direction: row;
}

.img_container {
  width: 80%;
  height: 100%;
  display: flex;
  justify-content: center;
  padding-left: 0.5em;
}

.img_container img {
  width: 250px;
  height: 380px;
}

.details_container {
  display: flex;
  flex-direction: column;
}

.description_overview h4 {
  font-size: 18px;
  text-align: justify;
  font-weight: bold;
  margin-bottom: 1em;
  padding: 0 0.8em;
}

.details_info {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 0.5em;
}

.details_skeleton {
  margin-bottom: 1em;
  font-weight: bold;
  margin-right: 0.2em;
}

.iframe_container {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.iframe-trailer {
  border: 1px solid pink;
  margin-top: 1em;
  height: 22em;
  width: 30em;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.5em;
}

.border_container {
  display: flex;
  justify-content: center;
}

.border {
  border: 2px solid rgb(110, 98, 98);
  width: 95%;
}

@media screen and (max-width: 1000px) {
  .modal_container {
    height: max-content;
  }

  .header_container h1 {
    font-size: 1.1em;
    text-align: center;
    cursor: default;
  }

  .description_overview h4 {
    font-size: 13px;
  }

  .img_container {
    width: 40%;
    height: 60%;
    display: flex;
    justify-content: center;
    padding-left: 0.5em;
  }

  .img_container img {
    width: 120px;
    height: 160px;
  }
  .iframe_container {
    margin: 1em 0;
    border: 2px solid red;
  }

  .iframe-trailer {
    height: 12em;
    width: 20em;
  }

  .details_info {
    font-size: 11px;
  }
}

@media screen and (max-width: 680px) {
  .iframe-trailer {
    height: 8em;
    width: 14em;
  }
}
@media screen and (min-width: 1001px) and (max-width: 1100px) {
  .iframe-trailer {
    height: 18em;
    width: 24em;
  }
}
